

export type RequestsType = {

}
export type initStateType = typeof initState

const initState = {
}

export const requestReducer = (state: initStateType = initState, action: requestReducerActionsType): initStateType => { // fix any
    switch (action.type) {
        case 'mona/requestReducer/CHANGE_REQUEST': {
            return {...state}
        }
        default: return state;
    }
};



export type requestReducerActionsType = ReturnType<typeof changeRequestAC>

//* ActionCreators ----------------------------------------------------------------------->
export const changeRequestAC = ()  => ({type: 'mona/requestReducer/CHANGE_REQUEST'} as const);